@use 'src/styles/global';

.text-field {
  position: relative;
  max-width: 320px;
  width: 100%;

  &__top {
    display: flex;
    justify-content: space-between;

    &--small {
      width: 266px;
    }
  }

  &__small {
    width: 266px;
  }

  &__title {
    font-weight: 700;
    font-size: 12px;
    color: global.$dark100;
    text-transform: uppercase;
  }

  &__description {
    font-weight: 400;
    font-size: 12px;
    color: global.$dark50;
    text-transform: uppercase;
  }

  &__input {
    // width: 320px;
    width: 100%;
    height: 44px;
    border: 1px solid global.$dark25;
    border-radius: 4px;
    padding-left: 15px;
    padding-right: 40px;
    font-size: 14px;
    color: global.$dark75;
    letter-spacing: -0.05px;
    word-spacing: -1px;

    &::placeholder {
      font-weight: 400;
      font-size: 14px;
      color: global.$dark25;
    }

    &:hover,
    &:focus {
      border: 1px solid global.$dark50;
      outline: none;
    }

    &:read-only {
      cursor: pointer;
    }

    &--small {
      width: 266px;
    }
  }

  &__arrow {
    position: absolute;
    top: 34px;
    right: 10px;
    color: global.$purple;

    &--small {
      top: 11px;
      right: 10px;
    }
  }
}
