.handler {
  z-index: 3;
  width: 16px;
  height: 16px;
  background: linear-gradient(#6fcf97 0%, #66d2ea 100%);
  border: 2px solid #fff;
  border-radius: 10px;
  position: absolute;
  top: -5px;
  transform: translateX(-50%);
}

.label {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  user-select: none;
  background: linear-gradient(#6fcf97 0%, #66d2ea 100%);
  border-radius: 2px;
  padding: 3px 5px;
  position: absolute;
  top: -35px;
  left: 0%;
  transform: translateX(-50%);
}

.progress-bar {
  z-index: 2;
  height: 6px;
  background: linear-gradient(#6fcf97 0%, #66d2ea 100%);
  position: absolute;
}

.line-block {
  box-sizing: border-box;
  width: 100%;
  height: 6px;
  cursor: pointer;
  touch-action: none;
  background: #fff;
  border-radius: 3px;
  outline: 1px solid #1f204140;
  position: relative;
}

.scale {
  cursor: pointer;
  margin-top: 10px;
  position: relative;
}

.scale__item {
  user-select: none;
  position: absolute;
  transform: translateX(-50%);
}

.js-slider {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: relative;
}

.js-slider[data-isvertical='true'] {
  flex-direction: row;
}

.js-slider[data-isvertical='true'] .line-block {
  width: 6px;
  height: 100%;
  touch-action: none;
  margin-left: 5px;
}

.js-slider[data-isvertical='true'] .line-block .handler {
  top: 0%;
  transform: translate(-30%, -5px);
}

.js-slider[data-isvertical='true'] .line-block .progress-bar {
  width: 100%;
}

.js-slider[data-isvertical='true'] .line-block ~ .scale {
  flex-direction: column;
  margin-top: 0;
  margin-left: -35px;
}

.js-slider[data-isvertical='true'] .line-block ~ .scale .scale__item {
  transform: translateY(-50%);
}

.js-slider[data-isvertical='true'] .line-block .label {
  margin-left: 30px;
  transform: translateY(-50%);
}

