@use 'src/styles/global';

.toggle {
  display: flex;

  &__block {
    display: flex;
    justify-content: flex-start;
    padding: 3px 7px 0 2px;
    width: 40px;
    height: 20px;
    border: 1px solid global.$dark25;
    border-radius: 10px;
    margin-right: 10px;
    cursor: pointer;

    &--active {
      border: 1px solid #bc9cff;
    }
  }

  &__circle {
    width: 12px;
    height: 12px;
    background: global.$dark25;
    border-radius: 10px;
    transition: 200ms;

    &--active {
      background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
      transform: translateX(22px);
      transition: 200ms;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: global.$dark75;
    transform: translateY(-3px);
  }
}
