@use 'src/styles/global';

.radio-button {
  margin: 0 20px 3px 0;
  display: flex;
  align-items: center;

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked + .radio-button__label {
      color: global.$dark75;

      &::before {
        background: url('../../images/Rectangle.png');
        border-color: global.$purple;
        background-repeat: no-repeat;
        background-position: center center;
      }
    }
  }

  &__label {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: global.$dark50;
    cursor: pointer;

    &::before {
      content: '';
      display: inline-block;
      width: 18px;
      height: 18px;
      background-color: #fff;
      border: 1px solid global.$dark25;
      border-radius: 50%;
      margin-right: 10px;
      transform: translateY(6px);
    }
  }
}
