@use 'src/styles/global.scss';

.booking {
  width: 380px;
  height: 512px;
  background: #fff;
  border: 1px solid rgb(0 0 0 / 12%);
  box-shadow: 0 0 25px rgb(0 0 0 / 20%);
  border-radius: 4px;
  padding: 45px 30px 30px;

  @media (max-width: 380px) {
    width: 100%;
    padding: 35px 0px 25px;
    border: none;
  }

  &__id {
    & .number-room-and-price__id {
      font-size: 23px;
      margin-right: 7px;
    }

    & .number-room-and-price__price {
      transform: translate(-3px, 1px);
    }

    & .number-room-and-price__period {
      transform: translate(0, 1px);
    }
  }

  &__date-dropdown {
    margin-top: 5px;
  }

  &__guest-dropdown {
    margin-top: 17px;
  }

  &__list {
    margin-top: 19px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 11px;
  }

  &__price-to-days {
    display: flex;
  }

  &__price-item {
    margin-right: 5px;
    width: 214px;
    line-height: 18px;
  }

  &__price-info {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid global.$dark25;
    transform: translateY(-1px);
  }

  &__price-symbol {
    color: global.$dark25;
    font-weight: 700;
    font-size: 12px;
    text-align: center;
    transform: translateY(-2px);
  }

  &__total-price {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }

  &__total-price-text {
    font-weight: 700;
    font-size: 19px;
    line-height: 23px;
    color: global.$dark100;
  }

  &__total-price-underline {
    border-bottom: 1px dotted global.$dark25;
    height: 23px;
    max-width: 180px;
    width: 100%;
    transform: translateY(-5px);
  }

  &__button {
    margin-top: 20px;
    transform: translateX(-2px);
  }
}
